import ajax from 'common/js/ajax'

export function handleDocReg(regForm) {
  const url = 'http://localhost:9090/doctor/add'

  const data = {
    name: regForm.name,
    password: regForm.pass,
    officeLocation: regForm.officeLocation,
    officePhone: regForm.officePhone,
    department: parseInt(regForm.department),
    degree: parseInt(regForm.degree),
    workage: parseInt(regForm.workAge),
    isFree: regForm.isFree,
    account: regForm.account,
    position: parseInt(regForm.position),
    title: parseInt(regForm.title)
  }

  return ajax('post', url, data).then((res) => {
    return Promise.resolve(res)
  })
}

export function handleDocLog(logForm) {
  // const url = 'http://localhost:9090/doctor/login'

  const data = {
    accout: logForm.account,
    password: logForm.password
  }

  return data
}



