<template>
  <div class="patient">
    <el-container class="container">
      <el-header class="header">
        <div class="header-l"> 
          <h1>欢迎使用Medicine系统</h1>
        </div>
        <div class="header-r"> 
          <span class="span">你好,病人</span><el-button size="mini" type="text" @click="logOut">退出</el-button>
        </div>
      </el-header>
      <el-main class="main">
        <el-tabs v-model="activeName">
          <el-tab-pane label="基本属性信息" name="first">
            <el-table
              :data="patientInfo"
              border
              style="width: 100%">
              <el-table-column
                prop="date"
                label="基本信息"
                width="180">
              </el-table-column>
              <el-table-column
                prop="name"
                label="姓名"
                width="180">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="可信属性信息" name="second">配置管理</el-tab-pane>
          <el-tab-pane label="病情相关信息" name="third">角色管理</el-tab-pane>
          <el-tab-pane label="评价医生" name="fourth">定时任务补偿</el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        activeName: 'first',
        patientInfo: ''
      }
    },
    methods: {
      logOut() {
        this.$router.push({ path: 'login' })
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .patient
    .container
      height:600px
      .header
        background-color: $color-background-f
        .header-l
          height: $font-size-large-x
          font-size: $font-size-large-x
          margin-top: 19px
          float:left
        .header-r
          height:28px
          font-size: $font-size-small
          float:right
          margin-top: 16px 
      .main
        height:600px
        background-color: $color-background-body
</style>



