<template>
  <div class="login">
    <el-container class="container">
      <el-header class="header">
        <div class="header-l"> 
          <h1>欢迎使用Medicine系统</h1>
        </div>
        <div class="header-r">
          <el-button-group>
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="primary" @click="register">注册</el-button>
          </el-button-group>
        </div>   
      </el-header>
      <el-main class="main">
      </el-main>
      <el-dialog
        title="登录"
        :visible.sync="loginVisible"
        width="30%"
      >
        <el-form ref="logForm" :model="logForm" label-width="80px">
          <el-form-item label="用户类型" prop="userType">
            <el-select v-model="logForm.userType" placeholder="请选择用户类型">
              <el-option label="医生" value="doctor"></el-option>
              <el-option label="病人" value="patient"></el-option>
              <el-option label="其他用户" value="others"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="用户名" prop="account">
            <el-input v-model="logForm.account"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="logForm.password" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('logForm')">提交</el-button>
            <el-button type="danger" @click="resetForm('logForm')">重置</el-button>
          </el-form-item> 
        </el-form>
      </el-dialog>
      <el-main class="main">
      </el-main>
      <el-dialog
        title="注册"
        :visible.sync="regVisible"
        width="40%"
      >
        <el-form ref="regForm" :model="regForm" label-width="80px" status-icon :rules="regRules">
          <el-form-item label="用户类型" prop="userType">
            <el-select v-model="regForm.userType" placeholder="请选择用户类型">
              <el-option label="医生" value="doctor"></el-option>
              <el-option label="病人" value="patient"></el-option>
              <el-option label="其他用户" value="others"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="真实姓名" prop="name">
            <el-input v-model="regForm.name"></el-input>
          </el-form-item>
          <el-form-item label="用户名" prop="account">
            <el-input v-model="regForm.account"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="regForm.pass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="regForm.checkPass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="职位" v-if="isDoc" prop="position">
            <el-select v-model="regForm.position" placeholder="请选择职位">
              <el-option label="普通员工" value='0'></el-option>
              <el-option label="主任" value=1></el-option>
              <el-option label="副主任" value=2></el-option>
              <el-option label="科长" value="3"></el-option>
              <el-option label="组长" value="4"></el-option>
              <el-option label="院长" value="5"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="科室" v-if="isDoc" prop="department">
            <el-select v-model="regForm.department" placeholder="请选择科室">
              <el-option label="心内科" value='0'></el-option>
              <el-option label="脑瘤科" value="1"></el-option>
              <el-option label="外科" value="2"></el-option>
              <el-option label="内科" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职称" v-if="isDoc" prop="title">
            <el-select v-model="regForm.title" placeholder="请选择职称">
              <el-option label="实习医师" value='0'></el-option>
              <el-option label="住院医师" value="1"></el-option>
              <el-option label="主治医师" value="2"></el-option>
              <el-option label="副主任医师" value="3"></el-option>
              <el-option label="主任医师" value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="办公地点" v-if="isDoc" prop="officeLocation">
            <el-input v-model="regForm.officeLocation" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="办公电话" v-if="isDoc" prop="officePhone">
            <el-input v-model="regForm.officePhone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="工龄" v-if="isDoc" prop="workAge">
            <el-select v-model="regForm.workAge" placeholder="请选择工龄">
              <el-option label="三年之内" value=0></el-option>
              <el-option label="三年以上" value=1></el-option>
              <el-option label="十年以上" value=2></el-option>
              <el-option label="二十年以上" value=3></el-option>
              <el-option label="三十年以上" value=4></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学位" v-if="isDoc" prop="degree"> 
            <el-select v-model="regForm.degree" placeholder="请选择学位">
              <el-option label="学士" value="0"></el-option>
              <el-option label="硕士" value="1"></el-option>
              <el-option label="博士" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否空闲" v-if="isDoc" prop="isFree">
            <el-select v-model="regForm.isFree" placeholder="请选择是否空闲">
              <el-option label="是" value="true"></el-option>
              <el-option label="否" value="false"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('regForm')">提交</el-button>
            <el-button type="danger" @click="resetForm('regForm')">重置</el-button>
          </el-form-item>      
        </el-form>
      </el-dialog>
    </el-container>
  </div>
</template>

<script type="text/ecmascript-6">
  import {handleDocReg, handleDocLog} from 'api/handleloginform'
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'))
        } else {
          if (this.regForm.checkPass !== '') {
            this.$refs.regForm.validateField('checkPass')
          }
          callback()
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.regForm.pass) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          callback()
        }
      }
      return {
        loginVisible: false,
        regVisible: false,
        regForm: {
          userType: '',
          name: '',
          pass: '',
          checkPass: '',
          officeLocation: '',
          officePhone: '',
          department: '',
          degree: '',
          workAge: '',
          isFree: '',
          account: '',
          position: ''
        },
        logForm: {
          userType: '',
          account: '',
          password: ''
        },
        regRules: {
          pass: [
            { validator: validatePass, required: true, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, required: true, trigger: 'blur' }
          ],
          department: [
            {required: true, message: '请选择科室', trigger: 'blur'}
          ],
          position: [
            {required: true, message: '请选择职位', trigger: 'blur'}
          ],
          officePhone: [
            {required: true, message: '请输入办公电话', trigger: 'blur'}
          ],
          degree: [
            {required: true, message: '请选择学位', trigger: 'blur'}
          ],
          userType: [
            {required: true, message: '请选择用户类型', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入真实姓名', trigger: 'blur'}
          ],
          title: [
           {required: true, message: '请选择职称', trigger: 'blur'}
          ],
          isFree: [
           {required: true, message: '请选择是否空闲', trigger: 'blur'}
          ],
          officeLocation: [
           {required: true, message: '请输入办公地点', trigger: 'blur'}
          ],
          workAge: [
           {required: true, message: '请选择工作年龄', trigger: 'blur'}
          ],
          account: [
            {required: true, message: '请输入用户名', trigger: 'blur'}
          ]
        },
        logRules: {
          account: [
            {required: true, message: '请选择工作年龄', trigger: 'blur'}
          ]
        }
      }
    },
    computed: {
      isDoc() {
        return this.regForm.userType === 'doctor'
      }
    },
    methods: {
      login() {
        this.loginVisible = true
      },
      register() {
        this.regVisible = true
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.handleForm(formName)
          } else {
            this.$message.error('请完成填写')
            return false
          }
        })
      },
      handleForm(formName) {
        if (formName === 'regForm') {
          if (this.regForm.userType === 'doctor') {
            this._docReg()
          }
        } else if (formName === 'logForm') {
          this._docLog()
        }
      },
      _docReg() {
        handleDocReg(this.regForm).then((res) => {
          if (res.msg === '') {
            this.$message({
              message: '注册成功',
              type: 'success'
            })
          }
        })
      },
      _docLog() {
        handleDocLog(this.logForm)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .login
    .container
      height:400px
      .header
        background-color: $color-background-f
        .header-l
          height: $font-size-large-x
          font-size: $font-size-large-x
          margin-top: 19px
          float:left
        .header-r
          float:right
          margin-top: 10px 
      .main
        height:300px
        background-color: $color-background-s
</style>





