import Vue from 'vue'
import Router from 'vue-router'
import Login from 'components/login/login'
import Doctor from 'components/doctor/doctor'
import Patient from 'components/patient/patient'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/patient'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/doctor',
      component: Doctor
    },
    {
      path: '/patient',
      component: Patient
    }
  ]
})



