<template>
  <div class="doctor">
    <el-container class="container">
      <el-header class="header">
        <div class="header-l"> 
          <h1>欢迎使用Medicine系统</h1>
        </div>
        <div class="header-r"> 
          <span class="span">你好,医生</span><el-button size="mini" type="text" @click="logOut">退出</el-button>
        </div>
      </el-header>
      <el-main class="main">
        <el-tabs v-model="activeName">
          <el-tab-pane label="基本属性信息" name="first">
            <el-table
              :data="doctorInfo"
              border
              style="width: 100%">
              <el-table-column
                prop="date"
                label="基本信息"
                width="180">
              </el-table-column>
              <el-table-column
                prop="name"
                label="姓名"
                width="180">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="可信属性信息" name="second">
            <el-table
              :data="doctorInfo"
              border
              style="width: 100%">
              <el-table-column
                prop="date"
                label="基本信息"
                width="180">
              </el-table-column>
              <el-table-column
                prop="name"
                label="姓名"
                width="180">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="主治病人集" name="third">
            <el-table
              :data="tableData"
              border
              style="width: 100%">
              <el-table-column
                label="编号"
                width="180"
                prop="date"
                >
              </el-table-column>
              <el-table-column
                label="姓名"
                width="180">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p>{{ scope.row.name }}</p>
                    <p>{{scope.row.date}}</p>
                    <div slot="reference" class="name-wrapper">
                      <span>{{ scope.row.name }}</span>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    plain
                    type="primary"
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">查看病人</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除病人</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="注销用户" name="forth">
            <el-alert
              show-icon
              title="确定要删除你的账户"
              description="执行该操作之后，将会删除你所有的信息"
              type="warning">
            </el-alert>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </div> 
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        doctorInfo: [{
          date: '属性1',
          name: '王小虎'
        }, {
          date: '属性1',
          name: '王大虎'
        }, {
          date: '属性1',
          name: '王中虎'
        }, {
          date: '2016-05-01',
          name: '王迷你虎'
        }],
        tableData: [{
          date: '2016-05-03',
          name: '王小虎'
        }, {
          date: '2016-05-02',
          name: '王大虎'
        }, {
          date: '2016-05-04',
          name: '王中虎'
        }, {
          date: '2016-05-01',
          name: '王迷你虎',
          province: '上海'
        }],
        activeName: 'first'
      }
    },
    methods: {
      logOut() {
        this.$router.push({ path: 'login' })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .doctor
    .container
      height:600px
      .header
        background-color: $color-background-f
        .header-l
          height: $font-size-large-x
          font-size: $font-size-large-x
          margin-top: 19px
          float:left
        .header-r
          height:28px
          font-size: $font-size-small
          float:right
          margin-top: 16px 
      .main
        height:600px
        background-color: $color-background-body
</style>


